import service from '../../service.js';
export default {
  data() {
    return {
      indicatorDots: true,
      autoplay: true,
      interval: 2000,
      duration: 500,
      indicatorActiveColor: '#ffffff',
      searchInfo: '大家都在搜“森海塞尔”',
      swiperList: [{}, {}, {}],
      tabList: [],
      selectIndex: 0,
      cultureList: []
    }
  },
  watch: {
    selectIndex(val) {
      this.getDefault(this.tabList[val].category_id)
    }
  },
  onLoad() {
    this.getCategorylist()
    this.getDefault()
  },
  methods: {
    // 获取文章
    getDefault(id) {
      this.$http({
        url: this.$api.articlesbycategoryid,
        data: {
          'category_id': id? id: ''
        },
        cb: (err, res) => {
          if (!err && res.code === 1) {
            this.cultureList = res.data.list
            if (res.data.list.length === 0) {
              uni.showToast({
              	title: '当前分类文章为空',
                icon: 'none'
              })
            }
            return
          } else {
            uni.showToast({
            	title: '文章列表获取失败',
              icon: 'none'
            })
          }
        }
      })
    },
    // 获取文章分类
    getCategorylist() {
      this.$http({
        data: {
          'wxapp_id': 10001,
          token: 'b612f5e2a32d553fdaea8eeb06bc2744',  
        },
        url: this.$api.categorylist,
        cb: (err, res) => {
          if (!err && res.code === 1) { 
            this.tabList = res.data.categoryList
            return
          } else {
            uni.showToast({
            	title: '文章分类获取失败',
              icon: 'none'
            })
          }
        }
      })
    },
    // 选择分类
    selectTab(item, index) {
      this.selectIndex = index
    }, 
    // 文章详情页
    goInfo(item) {
      uni.navigateTo({
        url: '../components/shareInfo?article_id=' + item
      })
    },
    // 搜索页
    goSearch() {
      uni.navigateTo({
        url: '../components/search'
      })
    }
  }
}
